ul {
  list-style: none;
  padding: 0;
}

.container {
  width: 500px;
  padding: 20px;
  margin: 0 auto;
}

.item {
  width: 400px;
  margin: 15px;
}

.drop-down-list {
  display: none;
}

.item:hover .drop-down-list {
  display: block;
  transition: display 1s linear;
}

.user-item {
  text-align: center;
  margin-bottom: 5px;
}
